import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";

import { CircularProgress, Menu, MenuItem, Fade } from "@material-ui/core";

import Table from "./Table";

import * as localStorageVariable from "../../variables/LocalStorage";
import instance from "../../services/AxiosServices";

import { accountAction, transactionAction } from "../../redux";

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

const Transaction = ({
  dispatch,
  receiversFromState,
  changeReceiversFromState,
}) => {
  const classes = useStyles();

  // Thng tin loi giao dch (cng hoc khc ngn hng)
  const [textTransactionType, setTextTransactionType] = useState(
    "Chuyn khon cng ngn hng"
  );
  const [transactionType, setTransactionType] = useState(0);
  const [
    disableButtonTransactionType,
    setDisableButtonTransactionType,
  ] = useState(false);

  // Thng tin ai l ngi tr ph giao dch
  const [textFormOfFeePayment, setTextFormOfFeePayment] = useState(
    "Ngi chuyn tr ph chuyn khon"
  );
  const [formOfFeePayment, setFormOfFeePayment] = useState(0);

  // Thng tin ti khon ngun
  const account = JSON.parse(
    localStorage.getItem(localStorageVariable.storeAccount)
  );
  const srcAccountNumber = account.accountNumber;
  const srcAccountName = account.accountName;

  // Thng tin ti khon ch
  const [desAccountNumber, setDesAccountNumber] = useState("");
  const [desAccountName, setDesAccountName] = useState("");

  // Thng tin danh b th hng
  const [receivers, setReceivers] = useState([]);

  // Thng tin chn 1 ti khon ch t danh b th hng
  const [select, setSelect] = useState();

  // Thng tin chuyn khon (s tin, ni dung)
  const [money, setMoney] = useState();
  const [content, setContent] = useState("");
  const [otp, setOtp] = useState("");

  // Thng tin progress hin ti, loi button hin ti
  const [progress, setProgress] = useState(0);
  const [textButton, setTextButton] = useState("Tip tc");
  const [status, setStatus] = useState("");

  // Thng tin thm danh b th hng
  const [accountNameReminiscent, setAccountNameReminiscent] = useState("");
  const [submit, setSubmit] = useState(false);

  // Thng tin giao dch thnh cng
  const [done, setDone] = useState("");

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const clearState = () => {
    setProgress(0);
    setTextButton("Tip tc");
    setStatus("");

    setTextTransactionType("Chuyn khon cng ngn hng");
    setTransactionType(0);
    setDisableButtonTransactionType(false);

    setTextFormOfFeePayment("Ngi chuyn tr ph chuyn khon");
    setFormOfFeePayment(0);

    setDesAccountNumber("");
    setDesAccountName("");
    setSelect(null);

    setMoney();
    setContent("");
    setOtp("");

    setAccountNameReminiscent("");
    setSubmit(false);
    setDone("");
  };

  // Gi action ly account khi account number thay i
  useEffect(() => {
    const setDesAccountNameFunction = async ()=>{
      if (transactionType === 0) {
        const internalAccount = await dispatch(accountAction.getAccount(desAccountNumber));
        if(internalAccount.status){
          setDesAccountName(internalAccount.data.accountName);
        }
      } else if (transactionType === 1) {
        const interbankAccount = await dispatch(transactionAction.getInterbankAccount(desAccountNumber));
        if(interbankAccount.status){
          setDesAccountName(interbankAccount.data.accountName);
        }
      }
    }
    setDesAccountNameFunction();
  }, [desAccountNumber]);

  // Ly danh b th hng t state
  if (changeReceiversFromState === true) {
    dispatch(accountAction.getReceivers());
  }

  useEffect(() => {
    let arr = [];
    if (transactionType === 0) {
      arr = receiversFromState.filter((i) => !i.isAnotherBank);
    } else if (transactionType === 1) {
      arr = receiversFromState.filter((i) => i.isAnotherBank === true);
    }
    setReceivers(arr);
  }, [transactionType, receiversFromState]);

  // Chn 1 ti khon ch trong danh b th hng
  useEffect(() => {
    if (select) {
      const selected = select[0];
      for (let i = 0; i < receiversFromState.length; i++) {
        if (receiversFromState[i]._id === selected) {
          setDesAccountNumber(receiversFromState[i].accountNumber);
          setDesAccountName(receiversFromState[i].accountName);
          break;
        }
      }
    }
  }, [select]);

  const HandleMainButtonClick = async () => {
    if (progress === 1 && (desAccountNumber === "" || desAccountName === "")) {
      return setStatus("Vui lng nhp s ti khon cn chuyn n");
    } else if (progress === 2 && (!money || content === "")) {
      return setStatus("Vui lng nhp y  thng tin cn thit");
    } else if (progress === 3 && otp === "") {
      return setStatus("Vui lng nhp y  thng tin cn thit");
    }

    if (progress === 2) {
      // X l gi otp
      instance.defaults.headers.common[
        "x_authorization"
      ] = localStorage.getItem(localStorageVariable.storeAccessToken);
      try {
        await instance.post("transactions/send-otp");
        setStatus("");
        setProgress(progress + 1);
      } catch (error) {
        return setStatus(error.response.data);
      }
    } else if (progress === 3) {
      instance.defaults.headers.common[
        "x_authorization"
      ] = localStorage.getItem(localStorageVariable.storeAccessToken);

      // X l xc nhn giao dch
      if (transactionType === 0) {
        try {
          const { data } = await instance.post("transactions/internal-bank", {
            formOfFeePayment,
            otp,
            desAccountNumber,
            content,
            money: +money,
          });

          const string = `Qu khch  chuyn thnh cng ${data.money} VND cho ${data.desAccountName} s ti khon ${data.desAccountNumber}.
                          S d ti khon ${data.delta} VND lc ${data.datetime}. S d ${data.accountMoney} VND`;
          setDone(string);

          setProgress(progress + 1);
          setStatus("");
        } catch (error) {
          return setStatus(error.response.data);
        }
      } else if (transactionType === 1) {
        try {
          const { data } = await instance.post("transactions/interbank", {
            formOfFeePayment,
            otp,
            desAccountNumber,
            desAccountName,
            content,
            money: +money,
          });

          const string = `Qu khch  chuyn thnh cng ${data.money} VND cho ${data.desAccountName} s ti khon ${data.desAccountNumber}.
                          S d ti khon ${data.delta} VND lc ${data.datetime}. S d ${data.accountMoney} VND`;
          setDone(string);

          setProgress(progress + 1);
          setStatus("");
        } catch (error) {
          return setStatus(error.response.data);
        }
      }
    } else {
      setStatus("");
      setProgress(progress + 1);
    }
  };

  useEffect(() => {
    if (progress === 0) {
      setTextButton("Tip tc");
      setDisableButtonTransactionType(false);
    } else if (progress === 1) {
      setTextButton("Tip tc");
      setDisableButtonTransactionType(true);
    } else if (progress === 2) {
      setTextButton("Tip tc");
      setDisableButtonTransactionType(true);
    } else if (progress === 3) {
      setTextButton("Chuyn ngay");
      setDisableButtonTransactionType(true);
    }
  }, [progress]);

  // X l thm danh b th hng
  const handleAddReceiver = () => {
    if (accountNameReminiscent === "") {
      setAccountNameReminiscent(desAccountName);
    }
    setSubmit(true);
  };
  useEffect(() => {
    if (submit) {
      const receiver = {
        accountNumber: desAccountNumber,
        accountName: desAccountName,
        accountNameReminiscent,
      };
      if (transactionType === 1) {
        receiver.isAnotherBank = true;
      }
      dispatch(accountAction.addReceiver(receiver));
      clearState();
    }
  }, [submit]);

  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="warning">
              <h4 className={classes.cardTitleWhite}>Chuyn khon</h4>
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <br />
                  <Button
                    color="primary"
                    style={{
                      width: "250px",
                    }}
                    disabled={disableButtonTransactionType}
                    onClick={() => {
                      setTextTransactionType("Chuyn khon cng ngn hng");
                      setTransactionType(0);
                    }}
                  >
                    Chuyn khon cng ngn hng
                  </Button>
                  <Button
                    color="primary"
                    style={{
                      width: "250px",
                    }}
                    disabled={disableButtonTransactionType}
                    onClick={() => {
                      setTextTransactionType("Chuyn khon lin ngn hng");
                      setTransactionType(1);
                    }}
                  >
                    Chuyn khon lin ngn hng
                  </Button>
                  <CircularProgress
                    size={200}
                    style={{
                      paddingTop: 50,
                    }}
                    variant="static"
                    color="secondary"
                    value={(progress * 100) / 4}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <h4>{textTransactionType}</h4>

                  {progress === 0 ? (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={4}>
                        <CustomInput
                          labelText="S ti khon ngun"
                          id="srcAccountNumber"
                          formControlProps={{
                            fullWidth: true,
                          }}
                          inputProps={{
                            disabled: true,
                          }}
                          value={srcAccountNumber}
                        />
                      </GridItem>
                      <GridItem xs={12} sm={12} md={8}>
                        <CustomInput
                          labelText="Tn ti khon ngun"
                          id="srcAccountName"
                          formControlProps={{
                            fullWidth: true,
                          }}
                          value={srcAccountName}
                          inputProps={{
                            disabled: true,
                          }}
                        />
                      </GridItem>
                    </GridContainer>
                  ) : null}

                  {progress === 1 ? (
                    <div>
                      <GridContainer>
                        <GridItem xs={12} sm={12} md={4}>
                          <CustomInput
                            labelText="S ti khon ch"
                            formControlProps={{
                              fullWidth: true,
                            }}
                            onChange={(event) => {
                              setDesAccountNumber(event.target.value);
                            }}
                            inputProps={{
                              disabled: false,
                            }}
                            value={desAccountNumber}
                          />
                        </GridItem>
                        <GridItem xs={12} sm={12} md={8}>
                        <CustomInput
                            // labelText="Tn ti khon ch"
                            formControlProps={{
                              fullWidth: true,
                            }}
                            inputProps={{
                              disabled: true,
                            }}
                            value={desAccountName}
                          />
                        </GridItem>
                      </GridContainer>
                      <GridContainer>
                        <GridItem xs={12} sm={12} md={12}>
                          <Table
                            rows={receivers}
                            setSelect={setSelect}
                          ></Table>
                        </GridItem>
                      </GridContainer>
                    </div>
                  ) : null}

                  {progress === 2 ? (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={4}>
                        <CustomInput
                          labelText="S tin"
                          id="money"
                          formControlProps={{
                            fullWidth: true,
                          }}
                          type="number"
                          value={money}
                          onChange={(event) => {
                            setMoney(event.target.value);
                          }}
                        />
                      </GridItem>
                      <GridItem xs={12} sm={12} md={8}>
                        <CustomInput
                          labelText="Ni dung"
                          id="content"
                          formControlProps={{
                            fullWidth: true,
                          }}
                          value={content}
                          onChange={(event) => {
                            setContent(event.target.value);
                          }}
                        />
                      </GridItem>
                    </GridContainer>
                  ) : null}

                  {progress === 2 ? (
                    <GridContainer
                      style={{
                        paddingTop: 20,
                      }}
                    >
                      <GridItem xs={12} sm={12} md={4}>
                        <Button
                          aria-controls="fade-menu"
                          aria-haspopup="true"
                          onClick={(event) => {
                            setAnchorEl(event.currentTarget);
                          }}
                        >
                          {textFormOfFeePayment}
                        </Button>
                        <Menu
                          id="fade-menu"
                          anchorEl={anchorEl}
                          keepMounted
                          open={open}
                          onClose={() => {
                            setAnchorEl(null);
                          }}
                          TransitionComponent={Fade}
                        >
                          <MenuItem
                            onClick={() => {
                              setTextFormOfFeePayment(
                                "Ngi chuyn tr ph chuyn khon"
                              );
                              setFormOfFeePayment(0);
                              setAnchorEl(null);
                            }}
                          >
                            Ngi chuyn tr
                          </MenuItem>
                          <MenuItem
                            onClick={() => {
                              setTextFormOfFeePayment(
                                "Ngi nhn tr ph chuyn khon"
                              );
                              setFormOfFeePayment(1);
                              setAnchorEl(null);
                            }}
                          >
                            Ngi nhn tr
                          </MenuItem>
                        </Menu>
                      </GridItem>
                    </GridContainer>
                  ) : null}

                  {progress === 3 ? (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={4}>
                        <CustomInput
                          labelText="OTP"
                          id="otp"
                          formControlProps={{
                            fullWidth: true,
                          }}
                          onChange={(event) => {
                            setOtp(event.target.value);
                          }}
                        />
                      </GridItem>
                    </GridContainer>
                  ) : null}

                  {progress === 3 ? (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={6}>
                        <h6
                          style={{
                            color: "green",
                          }}
                        >
                          Vui lng kim tra email  ly m xc thc
                        </h6>
                      </GridItem>
                    </GridContainer>
                  ) : null}

                  {progress === 4 ? (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={12}>
                        <h4 style={{ color: "green" }}>{done}</h4>
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}></GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <CustomInput
                          labelText="Tn gi nh"
                          id="accountNameReminiscent"
                          formControlProps={{
                            fullWidth: true,
                          }}
                          inputProps={{
                            disabled: false,
                          }}
                          onChange={(event) => {
                            setAccountNameReminiscent(event.target.value);
                          }}
                        />
                        <Button color="primary" onClick={handleAddReceiver}>
                          Lu danh b th hng
                        </Button>
                        <Button color="primary" onClick={clearState}>
                          Thc hin giao dch khc
                        </Button>
                      </GridItem>
                    </GridContainer>
                  ) : null}

                  <GridContainer>
                    <GridItem xs={12} sm={12} md={12}>
                      <h6
                        style={{
                          color: "red",
                        }}
                      >
                        {status}
                      </h6>
                      {progress !== 4 ? (
                        <div>
                          {progress !== 0 ? (
                            <Button
                              color="primary"
                              onClick={() => {
                                if (progress > 0) {
                                  setProgress(progress - 1);
                                  setStatus("");
                                }
                              }}
                            >
                              Tr li
                            </Button>
                          ) : null}
                          <Button
                            color="primary"
                            onClick={HandleMainButtonClick}
                          >
                            {textButton}
                          </Button>
                        </div>
                      ) : null}
                    </GridItem>
                  </GridContainer>
                </GridItem>
              </GridContainer>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    receiversFromState: state.receivers,
    changeReceiversFromState: state.changeReceivers,
  };
};

export default connect(mapStateToProps)(Transaction);
